import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { User } from '../models/user';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { JwtToken } from '../models/jwt-token.model';
import { tap } from 'rxjs/operators';

@Injectable()
export class AuthService {

  public jwtToken: BehaviorSubject<JwtToken> = new BehaviorSubject({
    isAuthenticated: null,
    token: null
  });
  private tokenKey: string = 'jwt';

  constructor(
    private httpClient: HttpClient
  ) {
    this.initToken();
   }

   private initToken(): void {
    const token = localStorage.getItem(this.tokenKey);
    if (token) {
      this.jwtToken.next({
        isAuthenticated: true,
        token: token
      })
    } else {
      this.jwtToken.next({
        isAuthenticated: false,
        token: null
      })
    }
   }

  public signup(user: User): Observable<User> {
    return this.httpClient.post<User>('/api/auth', user);
  }

  public signin(credentials: { email: string, password: string }): Observable<string> {

    return this.httpClient.post<string>('/api/auth/token', credentials).pipe(
      tap((token: string) => {
        this.jwtToken.next({
          isAuthenticated: true,
          token: token
        });
        localStorage.setItem(this.tokenKey,token);
      })
    );
  }
}



